.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Space Mono";
}

.header h1, .header h2 {
  margin: 25px 0 0;
}

.app__wrapper {
  display: flex;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}

.array__container {
  position: absolute;
  flex-direction: row;
  height: 200px;
  top: 200px;
  margin: 0 auto;
}

.bars {
  position: relative;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: flex-end;
  height: 100%;
}

.bars__background {
  cursor: crosshair;
  position: absolute;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: flex-end;
  height: 100%;
}

.bars__background div {
  z-index: 2;
}

.bars div {
  background: black;
}

.bars__background div, .bars div {
  height: 100%;
  width: 5px;
}

.controls {
  margin-top: 250px;
}

.button-primary {
  cursor: pointer;
  text-transform: uppercase;
  padding: 5px 10px;
  background: black;
  color: white;
}

.instructions {
  width: 30%;
  margin: 50px auto;
  font-family: "Space Mono";
  display: flex;
  flex-direction: column;
}

.instructions .row {
  display: flex;
  margin: 0 0 10px;
}

.instructions span {
  margin: 0 5px;
}